.modal-backdrop {
  background: #323c4a;

  &.in {
    opacity: 0.8;
  }
}

.modal {
  text-align: center;
  padding: 0!important;

  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -4px;
  }
}

.modal-dialog {
  display: inline-block;
  width: 500px;
  text-align: left;
  vertical-align: middle;
}

.modal-content {
  border-radius: 8px;
  border: 0;
  box-shadow: none;
}

.modal-header {
  padding: 18px 38px;
  border-bottom: 0;
  background: #fafbfb;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;

  .close {
    position: absolute;
    width: 14px;
    height: 14px;
    margin: 0;
    right: -26px;
    top: -26px;
    color: #fff;
    font-size: 1.4rem;
    opacity: 0.5;
  }
}

.modal-body {
  padding: 40px;

  .control-label {
    font-weight: 400;
    text-align: left;
  }
}

.modal-footer {
  display: flex;
  padding: 0;
  border-top: 0;

  .btn {
    margin: 0;
    width: 50%;
    height: 50px;
    border-radius: 0;
    border: 0;
    font-size: 1.8rem;
    font-weight: 500;

    & + .btn {
      margin: 0;
    }

    &:focus {
      outline: 0;
    }

    &:first-child {
      border-bottom-left-radius: 8px;
    }

    &:last-child {
      border-bottom-right-radius: 8px;
    }
  }

  .btn-default {
    background: #fafbfb;
    color: #ffb943;

    &:hover {
      background: lighten(#fafbfb, 5%);
    }
    &:active {
      background: darken(#fafbfb, 5%);
      box-shadow: none;
      color: #ffb943;
    }
  }

  .btn-primary {
    &:hover {
      background: lighten($text-color-link, 5%);
    }
    &:active {
      background: darken($text-color-link, 5%);
      box-shadow: none;
    }
  }
}