.wizard {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 20px;
  min-height: 70px;
  border-top: 1px solid $border-color-base;
  background-color: #fff;
  overflow: hidden;
  z-index: 200;

  .footer-logo {
    display: block;
    width: 111px;
    height: 95px;
    background: url("../img/logo-wizard.png") no-repeat;
    margin: -7px 18px -18px 20px;

    a {
      display: block;
      height: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-indent: 100%;
    }
  }
  .step-section {
    flex-grow: 0;
    position: relative;
    min-width: 290px;
    &:before {
      content: '';
      position: absolute;
      left: 100%;
      top: 50%;
      margin-top: -40px;
      width: 0;
      height: 0;
      border-top: 40px solid transparent;
      border-bottom: 40px solid transparent;

      border-left: 14px solid $border-color-base;
    }
    &:after {
      content: '';
      position: absolute;
      left: 100%;
      top: 50%;
      margin: -40px 0 0 -1px;
      width: 0;
      height: 0;
      border-top: 40px solid transparent;
      border-bottom: 40px solid transparent;

      border-left: 14px solid #fff;
    }
    h4 {
      color: $text-color-light;
      margin: 0;
    }
  }
  .caption-section {
    flex-grow: 1;
    padding: 0 40px;
    .icon {
      font-size: 1.8rem;
      margin-right: 4px;
      position: relative;
      top: 3px;
    }
    p {
      font-size: 1.3rem;
      margin: 0;
    }
  }
  .button-section {
    position: relative;
    &:before {
      position: absolute;
      content: '';
      width: 1px;
      height: 30px;
      left: 0;
      top: 50%;
      margin: -15px 0 0;
      background-color: $border-color-base;
    }
    flex-grow: 0;
    padding: 0 40px;
    .btn {
      min-width: 136px;
      text-align: center;
      .icon {
        margin-right: 0;
        margin-left: 7px;
      }
    }
  }
}