.alert {
  padding: 22px;
  margin-bottom: 20px;
  border: none;
  border-radius: 10px;
  position: relative;
  .close {
    position: absolute;
    top: 24px;
    right: 24px;
    font-size: 1.7rem;
  }
  p {
    font-size: 1.4rem;
    line-height: 1.5;
    margin: 0 0 1.4rem;
    &:last-child { margin-bottom: 0; }
  }
  h3 {
    margin:0 0 1.8rem;
  }
  .button-row {
    margin: 0;
    .btn {
      margin-left: 15px;
      color: #fff;
    }
    a { color: #62dc82; }
  }
  &.alert-small {
    padding: 12px 18px;
    .close {
      top: 14px;
      right: 16px;
    }
  }
}
.alert-success {
  background-color: rgba(98, 220, 130, 0.2);
  color: #47a05f;
  .close {
    color: #47a05f;
    opacity: 1;
  }
}

.btn {
  border-radius: 6px;

  &.btn-md{
    border-radius: 6px;
    padding: 8px 21px;
  }
  &.btn-lg{
    font-size: 1.4rem;
    line-height: 1.4;
    font-weight: 500;
    border-radius: 8px;
    padding: 11px 17px;
  }

  &.btn-success {
    background-color: $color-success;
    border-color: $color-success;
  }

  &.btn-default {
    border-width: 2px;
    border-color: $border-color-base;
    &:hover {
      background-color: #fff;
      border-color: $text-color-link;
    }
    &.active {
      box-shadow: none !important;
    }
  }

  &.btn-neutral {
    border-width: 2px;
    border-color: #ebebeb;
    background-color: #ebebeb;
    color: $text-color-base;
    &:hover {
      background-color: #ebebeb;
      border-color: $text-color-link;
    }
  }

  &.btn-custom {
    border-width: 2px;
    border-color: $text-color-link;
    color: #5591c4;
    background-color: #fff;
    &:hover {
      background-color: $text-color-link;
      color: #fff;
    }
  }

  &.btn-transparent {
    border-color: transparent;
    background-color: transparent;
    color: $color-warn;
  }

  &.btn-primary {
    border-color: transparent;
    background-color: $text-color-link;
  }
  &.btn-link {
    &:hover,
    &:focus { text-decoration: none; }
  }

  .icon {
    font-size: 2rem;
    margin-right: 7px;
    display: inline-block;
    vertical-align: top;
    position: relative;
    top: -1px;
    &.al-icon_close,
    &.al-icon_full-arrow-right,
    &.al-icon_full-arrow-left {
      font-size:1rem;
      top: 5px;
    }
  }
}
.form-control {
  height: 40px;
  padding: 9px 12px;
  border-radius: 6px;
  border: solid 2px $border-color-base;
  box-shadow: none;

  &:focus {
    box-shadow: none;
  }
}
.circle-icon {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  padding: 4px 0;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  margin-right: 15px;
  &.circle-facebook { background-color: #3a589b; }
  &.circle-instagram { background-color: #000; }
  &.circle-snapchat { background-color: $color-warn; }
}
.table>thead>tr>th { border-bottom: 4px solid #e6ebec; }
.table>tbody>tr>td,
.table>tbody>tr>th,
.table>tfoot>tr>td,
.table>tfoot>tr>th,
.table>thead>tr>td,
.table>thead>tr>th {
  border-top: none;
  vertical-align: middle;
  padding: 10px;
}
.table-striped>tbody>tr {
  background-color: #fafbfb;
  &:hover{
    background-color: rgba(98, 220, 130, 0.1);
  }
}
.table-striped>tbody>tr:nth-of-type(odd) {
  background-color: #fff;
  &:hover{
    background-color: rgba(98, 220, 130, 0.1);
  }
}
.controls {
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    display: inline-block;
    vertical-align: top;
    color: $text-color-light;
    font-size: 2rem;
    margin-left: 12px;
    &:first-child { margin-left: 0; }
    a {
      color: $text-color-light;
      &:hover,
      &:focus {
        text-decoration: none;
        color: $text-color-base;
      }
    }
  }
}

.input-group {
  .form-control {
    border-right: 0;
  }
}

.input-group-addon {
  background-color: #ebebeb;
  border-color: #ebebeb;
  font-weight: 600;
  color: $text-color-base;
  border-radius: 6px;
}
.reward-box {
  padding: 35px 37px;
  border-radius: 12px;
  border: dashed 2px $bg-color-default;
  &>img {
    float: left;
    margin: 3px 30px 0 0;
  }
  .oh {
    overflow: hidden;
    h4 {
      margin: 0 0 15px;
    }
    p {
      margin:0 0 24px;
    }
  }
}
.campaign-nav {
  padding-top: 18px;
  border-top: 1px solid $border-color-base;

  .btn {
    margin-left: 9px;
  }
}