.tooltip-holder {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  &:hover { overflow: visible; }

  &:after {
    content: '';
    height: 200%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 50%;
  }
}
.custom-tooltip {
  position: absolute;
  top: 100%;
  margin-top: 17px;
  left: 0;
  z-index: 10;
  background-color: #fff;
  min-width: 354px;
  border: solid 2px #ebebeb;
  border-radius: 10px;
  transition: opacity 0.5s;
  opacity: 0;

  &.visible { opacity: 1; }

  &:before,
  &:after{
    content: '';
    position: absolute;
    bottom: 100%;
    left: 30px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
  }

  &:before{
    border-bottom: 10px solid #ebebeb;
    margin-bottom: 2px;
  }

  &:after {
    border-bottom: 10px solid #fff;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 19px 17px 0;
    border-top: 2px dashed #ebebeb;
    &:first-child {
      border-top: none;
    }
    li {
      margin: 0 0 24px;
      padding-left: 42px;
      position: relative;

      .icon {
        position: absolute;
        left: 0;
        top: 5px;
        font-size: 2.7rem;
        color: #ddd;
      }
    }
  }
  .footer {
    padding: 15px 17px;
    background-color: #f8f8f8;
    .checkbox {
      font-weight: normal;
      padding-left: 42px;
      input[type="checkbox"] + .visual-checkbox {
        margin-left: -42px;
      }
    }
  }
}