.page-container {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.page-sidebar {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-shrink: 0;
  width: 250px;
  height: 100%;
  background: #323c4a;
  background: -moz-linear-gradient(top,  #323c4a 0%, #323c4a 89%, #2b333f 100%);
  background: -webkit-linear-gradient(top,  #323c4a 0%,#323c4a 89%,#2b333f 100%);
  background: linear-gradient(to bottom,  #323c4a 0%,#323c4a 89%,#2b333f 100%);
  color: #fff;

  .branding {
    min-height: 105px;
    .logo {
      display: block;
      width: 198px;
      height: 24px;
      margin: 28px auto;
      background: url("../img/logo.png") no-repeat;
      a {
        display: block;
        overflow: hidden;
        height: 100%;
        text-indent: 100%;
        white-space: nowrap;
      }
    }
  }

  .toggle-btn {
    position: absolute;
    top: 65px;
    right: -14px;
    z-index: 100;
    width: 28px;
    height: 28px;
    padding: 4px 7px;
    border-radius: 100%;
    background-color: $text-color-link;
    cursor: pointer;
    text-decoration: none;

    .icon {
      font-size: 1.2rem;
      color: #fff;
    }

    &:hover {
      background-color: #fff;
      .icon {
        color: $text-color-link;
      }
    }
  }

  .page-footer {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 25px 0;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 30px;
      right: 30px;
      height: 1px;
      background-color: rgba(255, 255, 255, 0.2);

    }
    .holder { padding: 0 30px; }

    p {
      margin: 0;
      color: rgba(255, 255, 255, 0.4);
    }
    .soc-links {
      list-style: none;
      margin: 0;
      padding: 13px 0 0;
      li {
        display: inline-block;
        vertical-align: top;
        font-size: 1.8rem;
        margin-left: 12px;

        &:first-child {
          margin-left: 0;
        }

        a {
          text-decoration: none;
          color: #000;
          color: rgba(255, 255, 255, 0.4);

          &:hover {
            color: rgba(255, 255, 255, 0.8);
          }
        }
      }
    }
  }
}

.page-header {
  position: fixed;
  top: 0;
  left: 250px;
  right: 0;
  z-index: 10;
  min-height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 0 20px 0 40px;
  margin:0;
  border: none;

  &:after {
    content: '';
    position: absolute;
    left: 44px;
    right: 0;
    bottom: 0;
    height: 1px;
    background-color: #e7e7e7;
  }
  .breadcrumbs {
    overflow: hidden;
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      vertical-align: top;
      color: $text-color-base;
      margin: 0 0 0 15px;

      & > a,
      & > span {
        display: inline-block;
        position: relative;
        padding-left: 30px;
        .icon {
          position: absolute;
          left: 0;
          top: 50%;
          color: $text-color-light;
          margin-top: -4px;
          font-size: 1rem;
        }
      }

      & > a {
        text-decoration: none;
        color: $text-color-light;

        &:hover {
          color: $text-color-base;
          .icon { color: $text-color-base; }
        }
      }

      &:first-child {
        margin-left: 0;
        & > a {
          padding-left: 26px;
          .icon {
            margin-top: -9px;
            font-size: 1.6rem;
          }
        }
      }
    }
  }
  .user-area {
    display: flex;
    align-items: center;

    .user-data {
      position: relative;
      padding: 0 50px 0 65px;

      .avatar {
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -25px;
        width: 50px;
        height: 50px;
        border-radius: 100%;
      }

      .name {
        display: block;
        font-weight: 400;
        color: #000;
      }

      .domain {
        display: block;
        font-size: 1.2rem;
        color: $text-color-light;
      }

      .btn-group {
        position: absolute;
        right: 0;
        top: 50%;
        margin: -25px 0 0;
        .btn {
          border: none;
          background: none;
          width: 50px;
          height: 50px;
          &:active {
            box-shadow: none;
          }
        }
        &.open {
          .btn {
            box-shadow: none;
          }
        }
        .dropdown-menu {
          left: auto;
          right: 0;
        }
        .icon {
          font-size: 1rem;
          margin-right: 0;
          vertical-align: middle;
          position: static;
        }
      }
    }

    .btn-logout {
      display: inline-block;
      font-size: 1.8rem;
      margin-left: 27px;
      position: relative;
      color: $text-color-light;
      text-decoration: none;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        margin: -10px 0 0 -22px;
        width: 1px;
        height: 20px;
        background-color: $border-color-base;
      }
    }
  }
}

.page-content {
  width: 100%;
  height: 100%;
  padding-top: 80px;
}

.main-content {
  height: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 70px;
  overflow: auto;
}

.page-section {
  margin-bottom: 70px;
}
.bootstrap-select.btn-group .dropdown-toggle .caret {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'al-icon-font' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: auto;
  height: auto;
  border: none;
  margin-top: -5px !important;
  font-size: 1rem;
  &:before {
    content: "\e923";
  }
}
.hint[data-toggle="tooltip"] {
  margin: 0 15px;
  font-size: 2rem;
}