.table.table-clean {
  & > tbody,
  & > tfoot,
  & > thead {
    & > tr {
      & > td,
      & > th {
        border-top: 0;
      }
    }
  }

  & > tbody {
    & > tr {
      & > td {
        padding: 15px 0;
        font-size: 2rem;
        vertical-align: middle;

        a {
          color: $text-color-base;

          &:hover {
            text-decoration: none;
          }
        }

        .indexes-weight-secondary & {
          font-size: 1.4rem;
        }
      }

      & > .col-overall,
      & > .col-permanent,
      & > .col-temp {
        .indexes-weight-secondary & {
          font-size: 1.8rem;
        }
      }
    }
  }

  & > thead {
    & > tr {
      & > th {
        border-bottom: 0;
        font-size: 1.4rem;
        font-weight: 400;
        color: rgba(56, 59, 62, 0.5);

        .indexes-weight-secondary & {
          font-size: 1.2rem;
        }
      }
    }
  }

  .text-muted {
    color: rgba(56, 59, 62, 0.5);
  }

  .text-important {
    font-weight: 500;
  }

  .col-weight {
    width: 20%;
  }
  .col-rating {
    width: 35%;
  }
  .col-overall,
  .col-permanent,
  .col-temp {
    width: 15%;
    text-align: right;
  }
}

.jq-ry-container {
  margin: auto;

  &.rating-small {
    display: inline-block;
    margin-right: 25px;
    vertical-align: middle;
  }
}