/* =custom checkbox */
.checkbox {
  padding-left: 30px;
  margin: 0;
  input[type="checkbox"] {
    position: absolute;
    z-index: 100;
    opacity: 0;
    & + .visual-checkbox {
      float: left;
      margin: 0 0 0 -30px;
      font-size: 14px;
      position: relative;
      width: 20px;
      height: 20px;
      border-radius: 4px;
      border: solid 2px $border-color-base;
      background-color: #fff;
      .icon {
        opacity: 0;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -7px 0 0 -7px;
        color: $color-success;
      }
    }
    &:checked + .visual-checkbox .icon {
      opacity: 1;
    }
    &:disabled + .visual-checkbox {
      background-color: $bg-color-default;
      &:after { background-color: $bg-color-default; }
    }
  }


  label:hover input[type="checkbox"] + .visual-checkbox,
  input[type="checkbox"] + .visual-checkbox:hover,
  input[type="checkbox"]:focus + .visual-checkbox {
    border-color: $color-success;
  }

  label:hover input[type="checkbox"]:disabled + .visual-checkbox {
    border-color: $bg-color-default;
  }

  input[type="checkbox"]:disabled + .visual-checkbox .icon {
    color: $bg-color-default;
  }
  &.disabled {
    color: $bg-color-default;
  }
}
/* custom checkbox */
.image-upload {
  .input-file {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  .image-upload-control {
    label {
      position: relative;
      border-radius: 12px;
      border: 2px dashed $border-color-base;
      padding: 20px;
      cursor: pointer;
    }
  }


  &.active {
    .image-upload-control {
      label {
        &::before {
          content: '\e92f';
          position: absolute;
          top: -2px;
          left: -2px;
          right: -2px;
          bottom: -2px;
          border-radius: 12px;
          background: rgba(#323c4a, 0.7);
          color: $border-color-base;
          font-family: 'al-icon-font' !important;
          speak: none;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          font-size: 3.6rem;
          font-weight: normal;
          line-height: 100px;
          text-align: center;
          vertical-align: middle;
        }
      }
    }
  }

  .image-upload-info {
    margin-left: 25px;
    color: $text-color-light;

    .name {
      margin-bottom: 5px;
    }
  }

  .progress {
    height: 4px;
    margin-bottom: 30px;
    border-radius: 2px;
    box-shadow: none;
    background: #e6ebec;

    .progress-bar {
      border-radius: 2px;
      background: $text-color-link;
      box-shadow: none;
    }
  }
}
