.column {
  padding-left: 22px;
  padding-right: 22px;
}

/* Row */
.flex-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;

  &.align-left {
    justify-content: flex-start;
  }

  &.align-right {
    justify-content: flex-end;
  }

  &.justified {
    justify-content: space-between;
  }

  &.centered {
    justify-content: center;
  }

  &.stretched {
    align-items: stretch;
  }

  &.align-end {
    align-items: flex-end;
  }

  &.align-middle {
    align-items: center;
  }

}

/* Column */
.flex-column {
  display: flex;
  flex-direction: column;

  &.justified {
    justify-content: space-between;
  }

  &.align-bottom {
    justify-content: flex-end;
  }

  &.align-top {
    justify-content: flex-start;
  }

  &.justified {
    justify-content: space-between;
  }

  &.centered {
    justify-content: center;
  }
}

/* Flex child alignment */
.align-center {
  align-self: center;
}

.align-start {
  align-self: flex-start;
}

.align-end {
  align-self: flex-end;
}

