/* Defaults */
html {
  font-size: 62.5%;
}
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
body {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-size: 1.4em;
  line-height: 1.5;
  font-weight: 400;
  color: #383b3e;
}

* {
  box-sizing: border-box;
}

img {
  display: block;
}

.link-with-icon {
  text-decoration: none;
  color: $text-color-link;
  .icon {
    display: inline-block;
    vertical-align: top;
    font-size: 2rem;
    margin-right: 6px;
  }
  &:focus,
  &:hover { text-decoration: none; }
}
.hint { color: $text-color-light; }
.text-danger { color: $color-danger }