.switch-label {
  font-size: 1.8rem;
  margin: 5px 0;
}
.switch {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 37px;
  height: 20px;
  margin-right: 15px;

  input {
    display:none;

    &:checked + .slider {
      background-color: $text-color-link;
      &:before {
        -webkit-transform: translateX(16px);
        -ms-transform: translateX(16px);
        transform: translateX(16px);
      }
    }
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $border-color-base;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 10px;

    &:before {
      position: absolute;
      content: "";
      height: 16px;
      width: 16px;
      left: 2px;
      bottom: 2px;
      background-color: #fff;
      -webkit-transition: .4s;
      transition: .4s;
      border-radius: 50%;
    }
  }
}