/* Page specific stuff */
.membership-levels {
  .membership-item {
    min-width: 202px;
    min-height: 187px;
    border-radius: 10px;
    border: solid 2px transparent;
    display: inline-block;
    vertical-align: top;
    padding: 15px 10px;
    margin-right: 15px;
    text-align: center;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;

    &:hover {
      border-color: $border-color-base;
      cursor: pointer;
    }

    .al-icon_membership {
      display: inline-block;
      vertical-align: top;
      margin: 0 0 6px;
      font-size: 5rem;
      &.basic { color: #5dd2d1;}
      &.bronze { color: #ffba43;}
      &.silver { color: #a9b0b8;}
      &.gold { color: #ffe013;}
      &.platinum { color: #e7eef6;}
    }

    h4 {
      color: $text-color-light;
      margin: 0;
      span { color: $text-color-base; }
    }
    .details-list {
      list-style: none;
      margin: 0 0 1.4rem;
      padding: 0;
      li {
        color: $text-color-light;
        font-size: 1.2rem;
        display: inline-block;
        vertical-align: top;
        position: relative;
        padding: 0 5px;
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          margin: -7px 0 0 -1px;
          width: 1px;
          height: 12px;
          background-color: $text-color-light;
        }
        &:first-child {
          &:before { display: none; }
        }
      }
    }
    & > img {
      display: inline-block;
      vertical-align: top;
    }
  }
}
.membership-header {
  margin: 0 0 30px;
  .al-icon_membership {
    display: inline-block;
    vertical-align: top;
    margin: 0 20px 0 0;
    font-size: 5rem;
    &.basic { color: #5dd2d1;}
    &.bronze { color: #ffba43;}
    &.silver { color: #a9b0b8;}
    &.gold { color: #ffe013;}
    &.platinum { color: #e7eef6;}
  }

  h4 {
    color: $text-color-light;
    margin: 0;
    span {
      color: $text-color-base;
      text-transform: capitalize;
    }
  }

  .details-list {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      color: $text-color-light;
      font-size: 1.2rem;
      display: inline-block;
      vertical-align: top;
      position: relative;
      padding: 0 5px;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        margin: -7px 0 0 -1px;
        width: 1px;
        height: 12px;
        background-color: $text-color-light;
      }
      &:first-child {
        padding-left: 0;
        &:before { display: none; }
      }
    }
  }
}
.dashboard-header {
  margin: 0 0 50px;
  header {
    padding: 13px 0 22px;
    .link-with-icon {
      display: inline-block;
      vertical-align: top;
      margin-top: 6px;
    }
  }
  .report-range-select {
    width: 248px;
    border-radius: 6px;
    border: solid 2px $border-color-base;
    margin-left: 60px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      margin: -13px 0 0 -30px;
      width: 1px;
      height: 25px;
      background-color: $border-color-base;
    }
    .icon-holder {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 44px;
      height: 44px;
      margin: -2px 0 -2px -2px;
      border-radius: 6px 0 0 6px;
      background-color: $text-color-link;
      font-size: 2.1rem;
      color: #fff;
    }
    & > .icon {
      margin: 0 10px;
      font-size: 1.2rem;
    }
  }
}

.item-list {
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    display: block;
    vertical-align: top;
    position: relative;
    margin:0 0 30px;
    padding-top: 15px;
    padding-bottom: 3px;

    &:before {
      content: '';
      position: absolute;
      top: -15px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #e7e7e7;
    }
    &:first-child {
      &:before { display: none; }
    }

    h4 { margin: 0 0 6px; }

    .controls {
      display: none;
      position: absolute;
      top: 15px;
      right: 20px;

      align-items: center;
      .btn-apply,
      .btn-remove {
        display: flex;
        align-items: center;
        justify-content: center;
        margin:0 0 0 12px;
        width: 38px;
        height: 38px;
        border-radius: 100%;
        background-color: #fff;
        border: solid 2px;
        &:hover { text-decoration: none; }
      }
      .btn-remove {
        border-color: $color-danger;
        color: $color-danger;
        &:hover {
          background-color: $color-danger;
          color: #fff;
        }
      }
      .btn-apply{
        border-color: $color-success;
        color: $color-success;
        &:hover {
          background-color: $color-success;
          color: #fff;
        }
      }
    }

    &:hover,
    &.active {
      .controls {
        display: flex;
      }
    }
    &.active {
      border-left: 5px solid $text-color-link;
      padding-left: 15px;
    }
    .status {
      display: block;
      color: $text-color-light;
      font-size: 1.2rem;
      margin: 0 0 12px;
    }

  }
}

.cards-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;

  li {
    min-width: 165px;
    min-height: 100px;
    border-radius: 10px;
    border: solid 2px $border-color-base;
    width: 31%;
    margin-right: 2%;
    margin-bottom: 20px;
    text-align: center;
    position: relative;
    padding: 15px;

    &:nth-child(3n) {
      margin-right: 0;
    }
    
    &:hover {
      border-color: #669de6;
    }

    .value {
      display: inline-block;
      vertical-align: top;
      font-size: 3.6rem;
      line-height: 1.25;
      position: relative;
      .icon {
        position: absolute;
        top: 10px;
        right: -18px;
        font-size: 1.2rem;
        &.positive { color: $color-success; }
        &.negative { color: $color-danger; }
      }
    }

    .param {
      display: block;
      font-size: 1.6rem;
      line-height: 1.25;
    }
  }
}
.colorpicker-element .add-on i,
.colorpicker-element .input-group-addon i { border-radius: 50% }
.inline-form-section {
  label {
    font-weight: normal;
    vertical-align: top !important;
    margin-right: 20px;
  }
}