.levels-list {
  display: table;
  table-layout: fixed;
  width: 100%;
  margin: -20px 0 50px;
  padding: 0 0 35px;

  .level-item {
    display: table-cell;
    position: relative;
    padding: 10px 0;
    border-top: 2px solid #e6eaef;
    border-bottom: 2px solid #e6eaef;
    background: url("../img/pattern.png");
    color: #fff;
    line-height: 1.8rem;
    text-align: center;

    .name {
      display: block;
      font-size: 1.6rem;
      font-weight: 500;
    }
    
    .customers {
      display: block;
      position: absolute;
      width: 100%;
      bottom: -35px;
      color: $text-color-base;
      font-size: 1.2rem;

      &::before {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        margin-right: 8px;
        border-radius: 50%;
      }
    }

    &:first-child {
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
      border-left: 2px solid #e6eaef;
    }

    &:last-child {
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
      border-right: 2px solid #e6eaef;
    }

    &.basic {
      width: 15%;
      border-color: $bg-basic;
      background: $bg-basic;

      .customers {
        &::before {
          background: $bg-basic;
        }
      }
    }

    &.bronze {
      width: 15%;
      border-color: $bg-bronze;
      background: $bg-bronze;

      .customers {
        &::before {
          background: $bg-bronze;
        }
      }
    }

    &.silver {
      width: 20%;
      border-color: $bg-silver;
      background: $bg-silver;

      .customers {
        &::before {
          background: $bg-silver;
        }
      }
    }

    &.gold {
      width: 20%;
      border-color: $bg-gold;
      background: $bg-gold;
      color: rgba($text-color-base, 0.7);

      .customers {
        &::before {
          background: $bg-gold;
        }
      }
    }

    &.platinum {
      width: 15%;
      border-color: $bg-platinum;
      background: $bg-platinum;
      color: rgba($text-color-base, 0.7);

      .customers {
        &::before {
          background: $bg-platinum;
        }
      }
    }

    &.undefined {
      cursor: pointer;
    }

    &.plus {
      width: 5%;
      border-color: #5a81b7;
      background: #5a81b7;
    }
  }
}