.main-menu {
  margin: 0 0 10px;
  padding: 0;

  li {
    display: block;
    list-style: none;
    cursor: pointer;

    & > a {
      display: block;
      padding: 15px 13px 14px;
      border-left: 7px solid transparent;
      font-weight: 500;
      color: #fff;

      &:hover {
        text-decoration: none;
      }
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
    }

    &.active {
      background: #fff;
      color: #383b3e;

      & > a {
        border-color: #669de6;
        color: $text-color-base;
      }

      .icon {
        color: $text-color-link;
        opacity: 1;
      }
    }

    .icon {
      display: inline-block;
      font-size: 2.1rem;
      color: #fff;
      opacity: 0.6;
      margin-right: 16px;
      vertical-align: top;
    }

    a {
      line-height: 21px;
      vertical-align: middle;
    }
    &.open {
      background-color: rgba(255, 255, 255, 0.1);
    }
    & > ul {
      display: none;
      list-style: none;
      padding: 0;

      li {

        a {
          padding: 15px 13px 14px 70px;
          color: rgba(255, 255, 255, 0.6);
          &:hover,
          &:focus {
            text-decoration: none;
          }

        }
        &.active a { color: #383b3e; }
      }
    }
    &.open ul { display: block; }
  }

}
.alert  {
  &.promo {
    padding: 45px 15px 16px;
    margin: 27px 29px 0 26px;
    border: solid 1px rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    text-align: center;

    .close {
      color: rgba(255, 255, 255, 0.2);
      top: 8px;
      right: 9px;
    }

    & > .icon {
      display: inline-block;
      font-size: 9.8rem;
      margin: 0 0 20px;
      color: rgba(255, 255, 255, 0.4);
    }

    p { color: rgba(255, 255, 255, 0.4); }
  }
}

@media screen and (max-height: 920px) {
  .page-sidebar {
    .promo {
      display: none;
    }
  }
}