/* Chart */
.chart-container {
  padding: 10px 0 25px;

  .ct-legend {
    padding: 50px 0 0;
    user-select: none;

    li {
      display: inline-block;
      margin: 0 20px;
      padding: 0;

      &:before {
        content: '\e900';
        display: inline-block;
        position: relative;
        width: 20px;
        height: 20px;
        margin-right: 15px;
        border-radius: 4px;
        vertical-align: middle;
        font-family: 'al-icon-font';
        line-height: 16px;
        color: #fff;
      }
    }
  }


  .ct-grid {
    stroke: #f0f0f0;
    stroke-dasharray: 7px;

    &.ct-solid {
      stroke: #c7ced3;
      stroke-dasharray: 0;
    }
  }

  .ct-label {
    font-size: 1.2rem;
    color: #383b3e;
  }


  .btn-group-sm {
    & > .btn {
      font-weight: 700;
      border-radius: 6px;
      border-width: 1px;
      border-color: $border-color-base;
    }
  }

  .btn-default {
    &:hover,
    &.active {
      background: $bg-color-default;
      box-shadow: none;
    }
  }
}

/* Legend */
.chart-legend {
  display: inline-block;
  margin: 0 0 10px;
  padding: 5px 20px;
  border: 1px solid $border-color-base;
  font-size: 1.2rem;
  line-height: 1.8rem;

  li {
    display: inline-block;

    & + li {
      margin-left: 10px;
    }
  }

  .legend-point {
    margin-right: 5px;
  }
}

/* Indexes */
.chart-indexes {
  position: relative;
  padding-left: 35px;

  .chartist-tooltip {
    padding: 0 0 57px 58px;
    text-align: left;
    background: transparent url("../img/tooltip-bg.png") 0 100% no-repeat;

    &::before {
      display: none;
    }
  }

  .chartist-tooltip-meta {
    color: #bbc4cb;
  }

  .chartist-tooltip-value {
    font-size: 2.4rem;
    line-height: 2.4rem;
    font-weight: 700;

    &:after {
      content: '%';
      font-weight: 700;
    }
  }
}

/* Sales */
.chart-sales {

  .ct-label.ct-vertical {
    &:after {
      content: '%';
    }
  }
}

/* Campaigns */
.chart-campaigns {

  .ct-label.ct-vertical {
    &:after {
      content: '$';
    }
  }
}

/* User actions */
.chart-actions {

  .ct-label.ct-vertical {
    &:after {
      content: 'min';
    }
  }

  .btn-group {
    margin-bottom: 10px;
  }
}

.chart-social,
.chart-loyalty-membership,
.chart-loyalty-social {
  position: relative;

  .chartist-tooltip {
    padding: 8px 22px;
    border-radius: 8px;
    border: 2px solid $border-color-base;
    background: #fff;

    &:before {
      top: calc(100% + 2px);
      left: 50%;
      width: 0;
      height: 0;
      margin-left: -10px;
      border: 10px solid transparent;
      border-top-color: $border-color-base;
    }

    &:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: calc(100% - 1px);
      left: 50%;
      margin-left: -10px;
      border: 10px solid transparent;
      border-top-color: #fff;
    }
  }

  .chartist-tooltip-meta {
    display: none;
  }

  .chartist-tooltip-value {
    display: block;
    padding-bottom: 1.4rem;
    font-size: 1.8rem;
    line-height: 1.8rem;
    font-weight: 700;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 5px;
      width: 100%;
      font-size: 1.2rem;
      line-height: 1.4rem;
      font-weight: 400;
      color: $text-color-light;
    }
  }
}

.chart-social {
  .chartist-tooltip-value {
    &:after {
      content: 'likes';
    }
  }
}
.chart-loyalty-social {
  .chartist-tooltip-value {
    &:after {
      content: 'users';
    }
  }
}

.chart-loyalty-membership {
  .ct-label.ct-horizontal {
    font-size: 1.8rem;
  }
  .chartist-tooltip-value {
    &:after {
      content: 'users';
    }
  }
}

.chart-indexes-info {
  padding: 0;
  margin: 0 0 0 -25px;

  li {
    display: inline-block;
    margin-left: 25px;
    font-size: 1.6rem;
    font-weight: 500;

    svg {
      margin-right: 9px;
    }

    .meta {
      color: $text-color-light;
    }
  }
}

.chart-loyalty-actions {
  .ct-legend {
    &:before {
      content: 'Membership levels';
      margin-right: 20px;
      font-weight: 500;
    }
  }
}

.chart-loyalty-dynamic {
  .ct-legend {
    &:before {
      content: 'Actions';
      margin-right: 20px;
      font-weight: 500;
    }
  }
}